import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route

} from "react-router-dom";
import Articulo from "./components/Articulo";
import Carrito from "./components/Carrito";
import Marticulos from "./components/MArticulos";
import Personal from "./components/Personal";
import { useAuth0 } from "@auth0/auth0-react";






function App() {
  const { loginWithRedirect } = useAuth0();

  return (
    <Router>
      <div className="App">

        <nav class="light-blue darken-4">
          <div className="nav-wrapper">
            <a href="#" class="brand-logo">
              <img src="https://preview.free3d.com/img/2017/02/2272884236681741608/te2hj2bq-900.jpg" width="60" height="60" >
              </img>

            </a>
            <ul id="nav-mobile" class="right hide-on-med-and-down">
              <li><a href="http://localhost:3000/usuarios">Administrar Usuarios</a></li>
              <li><a href="http://localhost:3000/articulos">Administracion de Articulo</a></li>
              <li><a href="http://localhost:3000/marticulos">Consulta de Articulos</a></li>
              <li><a href="http://localhost:3000/carrito">Carrito</a></li>



            </ul>
          </div>
        </nav>
        <button onClick={() => loginWithRedirect()}>login</button>

      </div>

      <Switch>
        <Route path="/articulos">
          <Articulo></Articulo>
        </Route>
        <Route path="/marticulos">
          <Marticulos></Marticulos>
        </Route>
        <Route path="/usuarios">
          <Personal></Personal>
        </Route>
        <Route path="/carrito">
          <Carrito></Carrito>
        </Route>


      </Switch>

    </Router>

  );
}

export default App;
