
import React from 'react';
import ReactDOM from "react-dom"
var total=0;

const PayPalButton = window.paypal.Buttons.driver("react", { React, ReactDOM });
class Carrito extends React.Component {
    createOrder(data, actions) {
        return actions.order.create({
          purchase_units: [
            {
              amount: {
                value: total,
                
              },
            },
          ],
        });
      }
    
      onApprove(data, actions) {
        return actions.order.capture();
      }
    
    constructor(props) {
        super(props);
        this.state = {
            Nombre: '',
            Descripcion: '',
            Precio: '',
            Img: '',
            _id: '',
            total: "",
            carrito: []

        }
    }
    prueba() {

        var carritostorage = window.localStorage.getItem("carrito", JSON.stringify(carritostorage))

        var car = JSON.parse(carritostorage)
        this.setState(
            {
                ...this.state,
                carrito: car,

            }
        );
        let sum = 0;
        
        for (let i = 0; i < car.length; i++) {
            sum += car[i].Precio;
            total=sum
        }
        console.log(sum);
    }
    componentDidMount() {
        this.prueba()
    }


    
    render() {
        return (
            <div>

                <div  >


                    {

                        this.state.carrito.map(articulos => {
                            return (
                                <div className="card text-center" style={{ width: 400 }} >
                                    <img className="card-img-top" src={articulos.Img} />
                                    <div className="card-body">
                                        <h5 className="card-title">{articulos.Nombre}</h5>
                                        <p class="card-text">{articulos.Descripcion}</p>
                                        <p class="card-text">${articulos.Precio}</p>

                                    </div>


                                </div>

                            )
                        })

                    }

                    <h2>
                     El total de la Compra es $ {total}   
                    </h2>
<PayPalButton
        createOrder={(data, actions) => this.createOrder(data, actions)}
        onApprove={(data, actions) => this.onApprove(data, actions)}
      />
    );
  

                </div>
            </div>
        );
    }
}

export default Carrito;